import { useCustomColors, useTheme } from "../shared";
import {
  Button,
  Drawer,
  DrawerBody,
  DrawerContent,
  DrawerHeader,
  DrawerOverlay,
  useDisclosure,
  Box,
  Text,
  Link,
  DrawerFooter,
  List,
  ListItem,
  Switch,
  HStack,
  useColorMode,
} from "@chakra-ui/react";
import { ArrowRightIcon, ArrowLeftIcon } from "@chakra-ui/icons";

const RenderDrawer = () => {
  const { toggleColorMode } = useColorMode();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const { fancy, toggleFancyMode } = useTheme();
  const { colorSoft, colorSoftAlternate, colorButtonBgHover } =
    useCustomColors();

  return (
    <>
      <Button
        pos="absolute"
        size="xs"
        id="sidebar-open-btn"
        onClick={onOpen}
        left="0px"
        top="20px"
        h="40px"
        bg={colorSoftAlternate}
        _hover={{ bg: colorButtonBgHover }}
        zIndex={3}
        borderStartRadius="0"
      >
        <ArrowRightIcon color={colorSoft} />
      </Button>
      <Drawer
        placement={"left"}
        onClose={onClose}
        isOpen={isOpen}
        userSelect={"none"}
        blockScrollOnMount={false}
        zIndex={2}
      >
        <DrawerOverlay />
        <DrawerContent w={"100%"} minW={"220px"} maxW={"300px"}>
          <Button
            onClick={onClose}
            size="xs"
            right="0px"
            top="20px"
            h="40px"
            w="80px"
            borderEndRadius="0"
            bg={colorSoftAlternate}
            _hover={{ bg: colorButtonBgHover }}
            position="absolute"
          >
            <ArrowLeftIcon color={colorSoft} />
          </Button>
          <DrawerHeader borderBottomWidth="1px">
            <Box w="100%" h="100%" p="7.5px">
              aitu map
            </Box>
          </DrawerHeader>
          <DrawerBody w="100%">
            <List spacing={3}>
              <ListItem>
                <Button
                  onClick={() => toggleColorMode()}
                  boxShadow={"lg"}
                  w="100%"
                >
                  <Text size="sm">change theme</Text>
                </Button>
              </ListItem>
              <ListItem>
                <Link href="https://github.com/Yuujiso/aitumap">
                  <Button boxShadow={"lg"} w="100%">
                    <Text size="sm">github</Text>
                  </Button>
                </Link>
              </ListItem>
              <ListItem>
                <Link href="https://t.me/yuujisoo/74">
                  <Button boxShadow={"lg"} w="100%">
                    <Text size="sm">about</Text>
                  </Button>
                </Link>
              </ListItem>
              <ListItem>
                <HStack w="100%" justify={"space-between"}>
                  <Text size="sm" as="samp">
                    fancy mode beta
                  </Text>
                  <Switch
                    size="lg"
                    isChecked={fancy}
                    onChange={toggleFancyMode}
                  />
                </HStack>
              </ListItem>
            </List>
          </DrawerBody>
          <DrawerFooter>
            <List w="100%" display="flex" flexDirection="column">
              <ListItem justify="space-between">
                <Link href="https://t.me/yuujisoo">
                  <Text as="samp">{"Developer :: @yuujisoo"}</Text>
                </Link>
              </ListItem>
              <ListItem>
                <Link href="https://t.me/meeezzanine">
                  <Text as="samp">{"Designer :: @meeezzanine"}</Text>
                </Link>
              </ListItem>
              <ListItem mt={"1rem"}>
                <Link href="https://t.me/yuujiso">
                  <Text as="samp">Report a problem/misleading information</Text>
                </Link>
              </ListItem>
            </List>
          </DrawerFooter>
        </DrawerContent>
      </Drawer>
    </>
  );
};

export default RenderDrawer;
