import { useContext, lazy, Suspense } from "react";
import { MapContext, Loading } from "../shared";
import { isIOS } from "mobile-device-detect";

const C1_ALL_1 = lazy(() => import("../shared/map_all/C1_ALL_1"));
const C1_ALL_2 = lazy(() => import("../shared/map_all/C1_ALL_2"));
const C1_ALL_3 = lazy(() => import("../shared/map_all/C1_ALL_3"));
const C1_ALL_1_IOS = lazy(() => import("../shared/map_all_ios/C1_ALL_1"));
const C1_ALL_2_IOS = lazy(() => import("../shared/map_all_ios/C1_ALL_2"));
const C1_ALL_3_IOS = lazy(() => import("../shared/map_all_ios/C1_ALL_3"));
const C1_1_1 = lazy(() => import("../shared/minimaps-all/C1_1_1"));
const C1_2_1 = lazy(() => import("../shared/minimaps-all/C1_2_1"));
const C1_3_1 = lazy(() => import("../shared/minimaps-all/C1_3_1"));
const C1_1_2 = lazy(() => import("../shared/minimaps-all/C1_1_2"));
const C1_2_2 = lazy(() => import("../shared/minimaps-all/C1_2_2"));
const C1_3_2 = lazy(() => import("../shared/minimaps-all/C1_3_2"));
const C1_1_3 = lazy(() => import("../shared/minimaps-all/C1_1_3"));
const C1_2_3 = lazy(() => import("../shared/minimaps-all/C1_2_3"));
const C1_3_3 = lazy(() => import("../shared/minimaps-all/C1_3_3"));

const RenderMapMaps = {
  B1F1: C1_1_1,
  B1F2: C1_1_2,
  B1F3: C1_1_3,
  B2F1: C1_2_1,
  B2F2: C1_2_2,
  B2F3: C1_2_3,
  B3F1: C1_3_1,
  B3F2: C1_3_2,
  B3F3: C1_3_3,
  F1: C1_ALL_1,
  F2: C1_ALL_2,
  F3: C1_ALL_3,
  F1_IOS: C1_ALL_1_IOS,
  F2_IOS: C1_ALL_2_IOS,
  F3_IOS: C1_ALL_3_IOS,
};

const RenderMap = () => {
  const { selectedBlockOption, selectedFloorOption } = useContext(MapContext);
  const Map = `${selectedBlockOption}${selectedFloorOption}${
    !selectedBlockOption && isIOS ? "_IOS" : ""
  }`;
  const Component = RenderMapMaps[Map];
  return (
    <Suspense fallback={<Loading />}>
      <Component />
    </Suspense>
  );
};

export default RenderMap;
