import { useColorModeValue } from "@chakra-ui/react";

const useCustomColors = () => {
  const colorFloorOptionBgOpacity = useColorModeValue("#4d525c", "#c8cdd4");
  const colorBgSelect = useColorModeValue("#242a36cf", "#edf2f7d3");
  const colorInputPlaceholder = useColorModeValue("#edf2f7a6", "#242a366a");
  const colorMapLayoutBackground = useColorModeValue("#4e4e4eff", "#363636ff");
  const colorMapLayoutFill = useColorModeValue("#ffffff", "#1a202c");
  const colorDrawerBg = useColorModeValue("#ffffff", "#2d3748");
  const colorButtonBg = useColorModeValue(
    "rgba(255, 255, 255, 0.08)",
    "#edf2f7"
  );
  const colorButtonBgActive = useColorModeValue(
    "#cbd5e0",
    "rgba(255, 255, 255, 0.24)"
  );
  const colorButtonBgHover = useColorModeValue(
    "#cbd5e0",
    "rgba(255, 255, 255, 0.37)"
  );
  const colorSoft = useColorModeValue("#edf2f7", "#242a36");
  const colorSoftAlternate = useColorModeValue("#242a36", "#edf2f7");

  const colorMapBlueDefault = "#4da2ff";

  return {
    colorFloorOptionBgOpacity,
    colorBgSelect,
    colorInputPlaceholder,
    colorMapLayoutBackground,
    colorMapLayoutFill,
    colorDrawerBg,
    colorButtonBg,
    colorButtonBgActive,
    colorButtonBgHover,
    colorSoft,
    colorSoftAlternate,
    colorMapBlueDefault,
  };
};

export { useCustomColors };
