import { isMobile, isMobileOnly } from "mobile-device-detect";
import { useEffect } from "react";

const useKeyboardAvoid = () => {
  useEffect(() => {
    if (!isMobile || !isMobileOnly) return;
    if (window.innerWidth > 768) return;
    if (!("ontouchstart" in window || navigator.maxTouchPoints > 0)) return;
    if (!/Mobi|Android/i.test(navigator.userAgent)) return;
    if (typeof window == "undefined") return;

    if ("virtualKeyboard" in navigator) {
      navigator.virtualKeyboard.overlaysContent = true;
    }

    let previousInnerHeight = window.innerHeight;
    let viewport = document.querySelector("meta[name=viewport]");

    function listenViewportHeight() {
      let currentInnerHeight = window.innerHeight;
      if (previousInnerHeight > currentInnerHeight) {
        viewport.setAttribute(
          "content",
          viewport.content + ", height=" + previousInnerHeight
        );
      }
    }

    window.addEventListener("resize", listenViewportHeight);

    return () => window.removeEventListener("resize", listenViewportHeight);
  }, []);
};

export { useKeyboardAvoid };
