import { useContext } from "react";
import { SearchIcon } from "@chakra-ui/icons";
import {
  Flex,
  Input,
  InputGroup,
  InputRightElement,
  Select,
  useMediaQuery,
} from "@chakra-ui/react";
import { MapContext, useCustomColors } from "../shared";

const Search = () => {
  const [isLessThan360] = useMediaQuery("(max-width: 360px)");

  const {
    isKeyboardTyping,
    selectedBlockOption,
    handleBlockOptionChange,
    search,
    handleSearchInput,
  } = useContext(MapContext);
  const {
    colorBgSelect,
    colorSoft,
    colorSoftAlternate,
    colorInputPlaceholder,
  } = useCustomColors();

  if ("virtualKeyboard" in navigator) {
    navigator.virtualKeyboard.overlaysContent = true;
  }

  return (
    <Flex
      pos={"absolute"}
      zIndex={2}
      transform={"translate(-50%, -50%)"}
      left={"50%"}
      top={"40px"}
    >
      <Select
        pointerEvents={!isKeyboardTyping ? "auto" : "none"}
        bg={colorBgSelect}
        color={colorSoft}
        value={selectedBlockOption}
        disabled={isKeyboardTyping}
        _disabled={{ opacity: 0.1 }}
        onChange={handleBlockOptionChange}
        borderEndRadius={0}
        borderStartRadius={9}
        width={"max-content"}
      >
        <option style={{ color: colorSoftAlternate }} value="">
          all
        </option>
        <option style={{ color: colorSoftAlternate }} value="B1">
          C1.1
        </option>
        <option style={{ color: colorSoftAlternate }} value="B2">
          C1.2
        </option>
        <option style={{ color: colorSoftAlternate }} value="B3">
          C1.3
        </option>
      </Select>
      <InputGroup w={isLessThan360 ? "100px" : "200px"}>
        <Input
          borderEndRadius={9}
          borderStartRadius={0}
          bg={colorSoftAlternate}
          color={colorSoft}
          type={"text"}
          width={"auto"}
          placeholder={"C1.2.223 or 2.223"}
          _placeholder={{ color: colorInputPlaceholder }}
          value={search}
          onChange={(e) => {
            handleSearchInput(e);
          }}
        />
        {!isLessThan360 && (
          <InputRightElement children={<SearchIcon color={colorSoft} />} />
        )}
      </InputGroup>
    </Flex>
  );
};

export default Search;
