import MapProvider from "./MapProvider";
import { RenderMap } from "../page";
import { Drawer, Search, FloorOption } from "../components";
import { useKeyboardAvoid } from "../shared";
import {
  ChakraProvider,
  ThemeProvider,
  theme,
  ColorModeProvider,
  CSSReset,
  Box,
} from "@chakra-ui/react";

function App() {
  useKeyboardAvoid();

  return (
    <MapProvider>
      <ChakraProvider>
        <ThemeProvider theme={theme}>
          <ColorModeProvider>
            <CSSReset />
            <Box pos={"relative"} w={"100dvw"} h={"100dvh"}>
              <FloorOption />
              <Search />
              <Drawer />
              <RenderMap />
            </Box>
          </ColorModeProvider>
        </ThemeProvider>
      </ChakraProvider>
    </MapProvider>
  );
}

export default App;
