import { useContext } from "react";
import { MapContext, useCustomColors } from "../shared";
import { Button, Flex } from "@chakra-ui/react";

const FloorOptionButton = () => {
  const { selectedFloorOption, floorOptionData, handleFloorOptionClick } =
    useContext(MapContext);

  const { colorSoftAlternate, colorSoft, colorMapBlueDefault } =
    useCustomColors();

  return (
    <Flex
      flexDirection="column"
      zIndex={2}
      pos="absolute"
      justify="space-between"
      align="center"
      transform="translate(-50%, -50%)"
      top="50%"
      right="0px"
      bg={colorSoftAlternate}
      borderRadius="20px"
      p="2.5px"
      w="35px"
      h="95px"
    >
      {floorOptionData.map((item) => {
        return (
          <Button
            key={item.id}
            name={item.name}
            onClick={handleFloorOptionClick}
            bg={
              selectedFloorOption === item.name ? colorSoft : colorSoftAlternate
            }
            _hover={{ bg: colorMapBlueDefault, transition: "0.4s ease" }}
            borderRadius={"15px"}
            color={
              selectedFloorOption === item.name ? colorSoftAlternate : colorSoft
            }
            fontSize={"1rem"}
            m={"0px"}
            size={"sm"}
            p={"0px"}
            width={"30px"}
            height={"30px"}
            maxW={"30px"}
            maxH={"30px"}
            display={"flex"}
            fontWeight={600}
            textAlign="center"
            border="none"
            justifyContent="center"
            alignItems="center"
          >
            {item.id}
          </Button>
        );
      })}
    </Flex>
  );
};

export default FloorOptionButton;
